.informacion{
    margin: 5%;
    justify-content: center;
    column-gap: 2rem;

    @include tablet{
        @include grid(2, $separacion);
    }
    
}
.informacion-imagen{
        max-width: 100%;
    }
    .informacion-texto{
        padding-top: 3rem;
        justify-items: center;
        text-align: center;
        h2{
            display: flex;
            flex-direction: column;
            justify-content: center;

            color: $negro;
            font-size: 4rem;
            align-items: center;
            
            
            span{
                color: $rosa;
                font-size: 2rem;
                padding: 4rem 2rem;
            }
        }
    }
    p{
        margin:0;
    }
    @include tablet{
        .informacion-texto{
            text-align: start;
            justify-items: start;
        }
    }