.header{
    background-color: $verde;

    .contenido-header{
        padding: $separacion / 2 0;

        @include tablet{
            display: flex;
            justify-content: space-between;
            align-items:  center;
        }
        
    }
    

    .navegacion-titulo{
        margin-bottom: 2rem;
        text-align: center;
        font-size: 2.5rem;
        font-weight: $bold;
        
        a{
            color: $blanco;
            
        }

        @include tablet{
            margin-bottom: 0;
            
        }
        @include telefono{
            font-size: 4rem;
        }
    }

    &.fijo{
        position: fixed;
        width: 100%;
        box-shadow: 0 .3rem 1rem rgb(32, 32, 32);
    }
    
    
}
.navegacion-principal{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 2rem;
    
    
    a{
        color: $blanco;
        font-size: 2rem;

        &:hover{
            color:$amarillo;
        }
    }
    @include telefono{
        flex-direction: row;
        a{
            font-size: 2rem;
        }
    }
}
