.galeria{
    padding: 5.6rem 0;

    h3{
        text-align: center;
    }
}

.galeria-imagenes{
    padding: 0;
    list-style: none;
    margin-top: $separacion;
    justify-items: center;
    @include grid(1, $separacion);
    gap: 2rem;
    

    @include telefono{
        @include grid(2, $separacion);
        gap: 3rem;
    }
    

    @include tablet{
        @include grid(4, $separacion/2)
    }
    @include desktop{
        @include grid(6, $separacion)
    }
}

.overlay{
    position:fixed;
    background-color: rgba(0,0,0,.5);
    
    width: 100%;
    height: 100%;
    top: 0;
    display: grid;
    place-items: center;
    
}
.btn-cerrar{
    width: 5rem;
    height: 5rem;
    background-color: $blanco;
    display: grid;
    place-items: center;
    font-weight: $bold;
    font-size: 3rem;
    border-radius: 50%;
    
    &:hover{
        cursor: pointer;
    }
}